import { createSlice } from '@reduxjs/toolkit';
import { createJSONBlob, createBinaryBlob, saveFile } from '../../app/utils';

const initialState = {
  lightbar: null,
  cycles: [],
  selectedCycle: 0,
  selectedLight: 0,
  showPreview: false,
  latestSelectedColor: null,
};

const getDefaultLights = (lightbar) =>
  [...Array(lightbar.front + lightbar.rear + 6).keys()].map((i) => ({
    color: {
      r: 0,
      g: 0,
      b: 0,
      a: 1,
    },
  }));

export const slice = createSlice({
  name: 'lightbar',
  initialState,
  reducers: {
    setLightbar: (state, action) => ({
      ...initialState,
      lightbar: action.payload,
    }),
    setSelectedCycle: (state, action) => {
      state.selectedCycle = action.payload;
    },
    setSelectedLight: (state, action) => {
      if (state.latestSelectedColor) {
        state.cycles[state.selectedCycle].lights[action.payload].color =
          state.latestSelectedColor;
      }

      state.selectedLight = action.payload;
    },
    addCycle: (state, action) => {
      state.cycles.push({
        lights: action.payload || getDefaultLights(state.lightbar),
      });
      state.selectedCycle = state.cycles.length - 1;
    },
    duplicateCycle: (state) => {
      state.cycles.push(state.cycles[state.selectedCycle]);
      state.selectedCycle = state.cycles.length - 1;
    },
    deleteCycle: (state, action) => {
      state.cycles.splice(action.payload, 1);
    },
    setLightSetting: (state, action) => {
      state.cycles[state.selectedCycle].lights[state.selectedLight] =
        action.payload;
      state.latestSelectedColor = action.payload.color;
    },
    setInitialState: (state) => {
      state = initialState;
    },
    setShowPreview: (state, action) => {
      state.showPreview = action.payload;
    },
    saveAsFile: (state, action) => {
      const [blob, type] = action.payload
        ? [
          createJSONBlob({ lightbar: state.lightbar, cycles: state.cycles }),
          'json',
        ]
        : [createBinaryBlob(state.cycles), 'bin'];

      saveFile(blob, `${state.lightbar.model}-${new Date().getTime()}.${type}`);
    },
  },
});

export const {
  setLightbar,
  setSelectedCycle,
  setSelectedLight,
  addCycle,
  duplicateCycle,
  deleteCycle,
  setLightSetting,
  setInitialState,
  setShowPreview,
  saveAsFile,
} = slice.actions;

export const getLightbar = (state) => state.lightbar.lightbar;
export const getCycles = (state) => state.lightbar.cycles;
export const getSelectedCycle = (state) => state.lightbar.selectedCycle;
export const getSelectedLight = (state) => state.lightbar.selectedLight;
export const getShowPreview = (state) => state.lightbar.showPreview;

export default slice.reducer;
