import Font from './Fonts/5x7-font';

export const textToBitmap = (text, padding = 0) => {
  const bitMap = [];

  [...Array(Font.row).keys()].forEach((r) =>
    [...Array(Font.column).keys()].forEach(() => {
      bitMap[r] = [];
      text.split('').forEach((char) => {
        bitMap[r].push(
          ...Font.chars[char][r]
            .toString(2)
            .padStart(char !== ' ' ? 5 : 1, 0)
            .split('')
            .map((v) => +v), // Adding the char in binary
          ...[...Array(padding).keys()].map(() => 0), // Adding padding to right
        );
      });
    }),
  );

  return bitMap;
};
