import React from 'react';
import {COLOR_SCHEMA} from './constants';
import {Select, makeStyles, MenuItem, Switch, FormGroup, FormControlLabel, withStyles} from '@material-ui/core';
import {useDispatch, useSelector} from 'react-redux';
import {selectColorSchema, getColorSchema, getHasGreen, setHasGreen} from './lightbar-selector-slice';
import {green} from '@material-ui/core/colors';


const useStyles = makeStyles((theme) => ({
  select: {
    width: '100%',
    marginTop: theme.spacing(2),
  },
  switch: {
    marginTop: theme.spacing(2),
  },
}));

const GreenSwitch = withStyles({
  switchBase: {
    color: green[300],
    '&$checked': {
      color: green[500],
    },
    '&$checked + $track': {
      backgroundColor: green[500],
    },
  },
  checked: {},
  track: {},
})(Switch);

function ColorSchemaList() {
  const classes = useStyles();
  const dispatch = useDispatch();
  const selectedSchema = useSelector(getColorSchema);
  const hasGreen = useSelector(getHasGreen);

  const onChange = e => dispatch(selectColorSchema(e.target.value));
  const onGreenSwithChange = e => dispatch(setHasGreen(e.target.checked));

  return (
    <>
      <Select
        value={selectedSchema || ''}
        onChange={onChange}
        displayEmpty
        className={classes.select}
        inputProps={{ 'aria-label': 'Without label' }}
      >
        <MenuItem value={null}>
          <em>---</em>
        </MenuItem>
        {
          COLOR_SCHEMA.map(schema =>
            <MenuItem
              key={schema.title}
              value={schema}
            >
                {schema.title}
            </MenuItem>
          )
        }
      </Select>
    { /*
      <FormGroup>
        <FormControlLabel
          className={classes.switch}
          control={
            <GreenSwitch
              name="hasGreen"
              onChange={onGreenSwithChange}
              value={hasGreen}
            />
          }
          label="Includes Green Light"
        />
      </FormGroup>
      */
    }
    </>
  );
}

export default ColorSchemaList;
