import React from 'react';
import {
  Card,
  CardContent,
  ButtonGroup,
  Button,
  makeStyles,
  CardHeader,
} from '@material-ui/core';
import { Redirect, useHistory } from 'react-router-dom';

const useStyles = makeStyles({
  root: {
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'center',
    marginTop: 20,
  },
  card: {
    width: '60vw',
  },
  cardContent: {
    textAlign: 'center',
  },
  button: {
    minHeight: 60,
  },
});

function Platforms() {
  const classes = useStyles();
  const history = useHistory();
  const route = (url) => () => history.push(url);

  return (
    <div className={classes.root}>
      <Card className={classes.card}>
        <CardHeader title="Select The Device" />
        <CardContent className={classes.cardContent}>
          <ButtonGroup
            className={classes.buttonGroup}
            orientation="vertical"
            color="primary"
          >
            <Button className={classes.button} onClick={route('/lightbars')}>
              Lightbars Configurator
            </Button>
            <Button
              className={classes.button}
              onClick={route('/message-board')}
            >
              Message Board Configurator
            </Button>
          </ButtonGroup>
        </CardContent>
      </Card>
    </div>
  );
}

export default Platforms;
