import { configureStore } from '@reduxjs/toolkit';
import lightbarSelectorReducer from '../features/LightbarSelector/lightbar-selector-slice';
import lightbarReducer from '../features/Lightbar/ligther-slice';
import scrollingBoardReducer from '../features/ScrollingBoard/scrolling-board-slice';

const store = configureStore({
  reducer: {
    lightbarSelector: lightbarSelectorReducer,
    lightbar: lightbarReducer,
    scrollingBoard: scrollingBoardReducer,
  },
});

export default store;
