import { createSlice } from "@reduxjs/toolkit";

export const slice = createSlice({
  name: 'lightbarSelector',
  initialState: {
    selectedModel: null,
    selectedColorSchema: null,
    hasGreen: false,
  },
  reducers: {
    selectLightbar: (state, action) => {
      state.selectedModel = action.payload;
    },
    selectColorSchema: (state, action) => {
      state.selectedColorSchema = action.payload;
    },
    setHasGreen: (state, action) => {
      state.hasGreen = action.payload;
    },
  },
});

export const {
  selectLightbar,
  selectColorSchema,
  setHasGreen,
} = slice.actions;

export const getLightbar = state => state.lightbarSelector.selectedModel;
export const getColorSchema = state => state.lightbarSelector.selectedColorSchema;
export const getHasGreen = state => state.lightbarSelector.hasGreen;

export default slice.reducer;
