import React from 'react';
import PropTypes from 'prop-types';
import { SketchPicker } from 'react-color';
import { makeStyles } from '@material-ui/core';
import SchemaColorPicker from './schema-color-picker';

const useStyles = makeStyles({
  root: {
    position: 'fixed',
    margin: '-50px 40px',
    zIndex: 2,
    backgroundColor: 'white',
  },
  pallete: {
    backgroundColor: 'white',
    position: 'absolute',
    zIndex: '2',
  },
  innerPallete: {
    position: 'fixed',
    top: 0,
    right: 0,
    bottom: 0,
    left: 0,
  },
});

const ColorPicker = ({ colors, value, onClick, onChange }) => {
  const classes = useStyles({ colors });

  return (
    <div className={classes.root}>
      <div className={classes.pallete}>
        <div className={classes.innerPallete} onClick={onClick} />
        {colors?.length ? (
          <SchemaColorPicker
            colors={colors}
            hex={false}
            triangle="hide"
            onChange={onChange}
          />
        ) : (
          <SketchPicker
            presetColors={[
              '#FFBF00',
              '#FF0000',
              '#00FF00',
              '#0000FF',
              '#000000',
            ]}
            color={value}
            onChange={onChange}
            disableAlpha
          />
        )}
      </div>
    </div>
  );
};

ColorPicker.propTypes = {
  value: PropTypes.object,
  onChange: PropTypes.func,
  onClick: PropTypes.func,
};

export default ColorPicker;
