import React from 'react';
import { makeStyles } from '@material-ui/core';
import { Switch, Route } from 'react-router-dom';

import { LightbarSelector } from './features/LightbarSelector';
import Header from './app/header';
import Lightbar from './features/Lightbar';
import ScrollingBoard from './features/ScrollingBoard';

import './App.css';
import Platforms from './features/Platforms';

const useStyles = makeStyles({
  root: {},
});

function App() {
  const classes = useStyles();
  return (
    <div className={classes.root}>
      <Header />
      <Switch>
        <Route path="/lightbars">
          <LightbarSelector />
        </Route>
        <Route path="/lightbar">
          <Lightbar />
        </Route>
        <Route path="/message-board">
          <ScrollingBoard />
        </Route>
        <Route path="/">
          <Platforms />
        </Route>
      </Switch>
    </div>
  );
}
export default App;
